body{
  background-color: #282c34;
  color: white;
}

.column {
  /* float: left; */
  width: 33.33%;
  border: 5px solid rgb(150, 145, 145);
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

.inputArea{
  height: 100%;
  width: 100%;
}

.listItem{
  padding: 0.2em;
}
